//
// CKeditor
//


//
// Header
//
header#navbar{
    padding-bottom: 10px;

    &:after{
        content:"";
        background: $cp1; /* Old browsers */
        background: -moz-linear-gradient(left, $cp1 0%, $cp1 20%, $cp2 20%, $cp2 40%, $cp3 40%, $cp3 40%, $cp3 60%, $cp4 60%, $cp4 80%, $cp5 80%); /* FF3.6-15 */
        background: -webkit-linear-gradient(left, $cp1 0%,$cp1 20%,$cp2 20%,$cp2 40%,$cp3 40%,$cp3 40%,$cp3 60%,$cp4 60%,$cp4 80%,$cp5 80%); /* Chrome10-25,Safari5.1-6 */
        background: linear-gradient(to right, $cp1 0%,$cp1 20%,$cp2 20%,$cp2 40%,$cp3 40%,$cp3 40%,$cp3 60%,$cp4 60%,$cp4 80%,$cp5 80%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='$cp1', endColorstr='$cp5',GradientType=1 ); /* IE6-9 */
        display: block;
        height:10px;
        width: 100%;
        position: absolute;
        bottom: 0;

    }
}
