// Menu
//

// Submenu


//
// Body Content
//

// Sidebar
.region-sidebar-first{
    .views-row{
        // background: lighten($cp4, 20%);
        // border-bottom: 5px solid $cp4;
        padding: 20px;
        margin-bottom: 20px !important;
    }

    .view-events .view-row{
        background: lighten($cp5, 20%) !important;
        border-bottom: 5px solid $cp5 !important;
    }

    .view-promotions .view-row{
        background: lighten($cp3, 20%) !important;
        border-bottom: 5px solid $cp3 !important;
    }

}

.view-ads .views-row {
    margin-bottom: 20px;
}

.col-1, .col-2{
    width: 100% !important;
}

.col-3.views-col{
    width: 100% !important;
    margin-bottom: 20px !important;
}

.views-exposed-form{
    background: $odd;
    padding: 20px;
    margin-bottom: 20px;
    .form-item{
        margin-bottom: 20px;
    }

    #edit-field-9-18-hole-text-value--wrapper{
        .form-item{margin: 0;}
    }


    .sr-only{
        display: block;
        position: relative;
        clip: unset;
        height: auto;
        width: auto;
    }
}


// Chosen
.chosen-container-single .chosen-single{
    border: none;
    background: none;
    box-shadow: none;
}

.field--name-field-name-of-instructor-s-{

    .table > thead > tr > th, .table > tbody > tr > th, .table > tfoot > tr > th, .table > thead > tr > td, .table > tbody > tr > td, .table > tfoot > tr > td{
        width: 100%;
        padding: 0;
    }

    td{
        padding-left: 0 !important;
    }

    tr{
        width: 100%;
        max-width: 100%;
    }

    .sr-only{
        width: auto;
        height: auto;
        clip: unset;
    }
}

.details{
    .field--label, .field--items{
        display: block;
        width: 100%;
    }
    div{
        margin-bottom: 10px;
    }
    a{
        word-break: break-all;
    }
}

.layout__region.layout__region--first-above {
    flex: none;
    width: 100%;
}
// Banners

//
// Footer
//
.view-footer{
    background: $cp2;
    padding: 10px;
    margin-bottom: 20px !important;
    *{
        color: white;
    }
}

.view-content, table{
    margin-bottom: 0 !important;
}

