// NOTE:: Applies from 1000px onwards starts effecting desktop and larger at this point
@media (min-width: 1000px){


    .page-node-type-golf-cources{
        tbody{
            tr{
                @include flex(50%)
                flex-basis: 50%;
                max-width: 50%;
            }
        }

        .field--name-field-logo{
            float: left;
            width: 25%;
        }
    }

    .col-3{
        max-width: 300px;
        padding: 0 !important;
        border: none !important;
        .views-row{
            border-bottom: none !important;
        }
    }

    .layout__region--second-above{
        padding-left: 20px;
    }

}
