// Applies from 768px onwards generally starts effecting tablets at this point
@media (min-width: 768px){
    //
    // Tables
    //
    th{
        background: $cp2;
        color: white;
    }

    // headers
    .pull-left{
        float: left !important;
    }

    .navbar .logo{
        max-width: 200px;
        display: block;
        margin: 20px 0;
    }

    header#navbar{
        position: sticky;
        top: 0;
        left: 0;
        right: 0;
        z-index: 1000;
        .logo{
            height: 70px;
            img{
                height: 100%;
                width: auto;
            }
        }
        .navbar-nav{
            margin-top: 28px;
        }
    }

    // Columns
    .ds-2col > .group-left, .ds-2col > .group-right{
        width: 50%;
    }

    .path-frontpage{
        .ds-2col-stacked > .group-left{
            width: 25%;
        }
        .ds-2col-stacked > .group-right{
            width: 75%;
        }
    }


    .col-1, .col-2{
        width: 50% !important;
        margin: 0;
        padding: 20px;
    }
    .col-1:nth-of-type(odd){
        border-right: 1px solid #e1e1e6;
    }
    .views-row{
        border-bottom: solid 1px #e1e1e6;
        margin: 0;
    }

    .form-inline .form-group{
        display: block;
        label{
            display: block;
        }
    }

    .form-inline .form-control{
        display: block;
        width: 100%;
    }

    .footer{
        a{
            color: $cp2;
        }
        .menu {
            a{
                display: inline-block;

                @include font-size(16px);
                padding: 8px 0;
                &:hover{
                    background: none;
                    color: $cp3;
                }
            }
        }
    }

.layout__region.layout__region--first-above{
    width: auto;
    flex: 0 1 50%;
}

}
