// NOTE:: This is the core HTML styles that can be found on the Style Guide Page of the site.


/*=======================
       Imports
========================*/
@import url('https://fonts.googleapis.com/css?family=Merriweather:300,400,400i,900|Montserrat:300,400,400i,600,800');
@import 'mixins';
@import 'colors';
//
// Font Familys
//
$special: 'Merriweather';

$headline: 'Merriweather', "Times New Roman", Times, serif;;

$bodyCopy: 'Montserrat', Helvetica, Arial, "Lucida Grande", sans-serif;

$radius: 0;
$fullradius: 100px;

//
// Core HTML
//
*, *:after, *:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.pull-left{
    float: none !important;
}

html{
    font-family: $bodyCopy;
    font-weight: 400;
    color: $text-gray;
    font-size: 17px;

}

body{
    margin-top: 0px;
}

.wrapper, .navbar-header, .container, .region-footer{
    max-width: 1300px;
    width: auto;
    margin: 0 auto !important;
    padding: 0 20px;
}

.ui-state-active .ui-icon .resizable-textarea .grippie, .element-invisible, div#skip-link, .toolbar-toggle-orientation{
    display: none !important;
}

// This removes the padding on the main body content so that everything is flush to the sides of the window.
.main-container > .row > .col-sm-12, .container-fluid{
    padding: 0;
}

// Headers

h1, h2, h3, h4, h5, h6{
    font-family: $headline;
    font-weight: 300;
    margin: 0;
    line-height: 1.5em;
}

h1{
    @include font-size(36px);
    font-weight: 700;
    color: $cp2;
    text-transform: uppercase;
    line-height: 1em;
  &.page-header{
    display: none;
  }
}

h2{
    @include font-size(28px);
    font-weight: 400;
    color: $cp1;

}

h3{
    @include font-size(26px);
    font-weight: 400;
    color: $cp5;
}

h4{
    @include font-size(20px);
    font-weight: 800;
    color: $cp4;
}

h5{
    @include font-size(20px);
    font-weight: 700;
    color: $cp1;
}

h6{
    @include font-size(18px);
    color: $cp3;
    font-weight: 800;
}

.footnote p{
    @include font-size(12px);
    font-weight: 800;
    margin-bottom: 0;
}

.headers{
    *{
        font-family: $headline !important;
    }
    li{
        margin: 0 !important;
        padding: 0 !important;
        &:before{
            display: none !important;
        }
    }
}

img{
    width: 100%;
    height: auto;
}
//
// Logo/Name
//
.navbar .logo{
    max-width: 150px;
    display: block;
    margin: 20px auto 0;
}

.logo-menu{
    margin-bottom: 0 !important;
}

.navbar-header{
    float: none;
}

.navbar-brand{
    float: none;
    height: auto;
    max-width: 1170px;
    margin: 0 auto;
}

.name{
display: none;
}

// Fix full width issue
.nav-tabs{
    max-width: 1170px;
    margin: 0 auto;
}

.bodycopy{
    *{
        font-family: $bodyCopy !important;
    }
    .light{
        font-weight: 100;
    }
    li{
        margin: 0 !important;
        padding: 0 !important;
        &:before{
            display: none !important;
        }
    }
}

.larger{
    text-transform: none;
    @include font-size(120px);
    line-height: 120px;
    margin-bottom: 0;
}

// Color Palette

.palette{
    float:left;
   width:100%;
   overflow:hidden;
   position:relative;
    margin-bottom: 20px !important;
    ul{
        clear:left;
        float:left;
        list-style:none;
        margin:0;
        padding:0;
        position:relative;
        left:50%;
        text-align:center;
    }
    li{
        display:block;
        float:left;
        list-style:none;
        position:relative;
        right:50%;
        width: 150px;
        height: 150px;
        position: relative;
        list-style: none;
        color: white;
        display: inline-block;
        text-align: center;
        margin-bottom: 20px;
        margin-right: 20px;
        &:before{
            display: none !important;
        }
        &:last-child{
            margin-right: 0;
        }
        p{
            color: white;
            position: absolute;
            @include font-size(20px);
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }
    }

    .cp1{
        background-color: $cp1;
    }
    .cp2{
        background-color: $cp2;
    }
    .cp3{
        background-color: $cp3;
    }
    .cp4{
        background-color: $cp4;
    }
    .cp5{
        background-color: $cp5;
    }
}

// Paragraph Styles

div, p{
    color: $text-gray;
    @include font-size(18px);
    margin:0 0 1em;
    line-height: 1.65em;
    font-weight: 400;
    font-family: $bodyCopy;
    &:last-child{
        margin-bottom: 1em;
    }
}

#toolbar-bar{
    *{
        @include font-size(12px);
        font-weight: 100;
    }
}

.container div:last-child, container p:last-child{
    margin: 0;
}

// .main-container .container div:last-child, .main-container .container p:last-child{
//     margin: 1em;
// }


i, em{
    font-style: italic;
    font-weight: 300;
}

b, strong{
    font-weight: 800;
    color: $cp4;
}

a{
    text-decoration: none;
    @include transition(color .3s ease);
    color: $cp3;
    font-weight: 800;
    &:hover{
        color: $cp1;
    }
}

a:hover, a:focus{
    text-decoration: none;
}

blockquote {
    border: none;
    margin: 0 0 20px;
    padding: 20px 20px 20px 60px;
    position: relative;
    @include font-size(20px);
    color: $cp2;
    line-height: 1.8em;
    font-weight: 800;
    position: relative;
    &:before{
        content: "\201C";
        @include font-size(80px);
        position: absolute;
        left: 0;
        top: 30px;
    }
    p{
        @include font-size(22px);
        color: $cp2;
        line-height: 40px;
        &:last-child{
            margin: 0;
        }
    }
}

.breadcrumb{
    display: none;
}

img{
    max-width: 100%;
    height: auto;
}
// Panels
@media (max-width: 768px){
.panels-flexible-region, .panel-panel{
    width: 100%;
}
}

.hidden{
    display: none;
}
.main-container{
    div:last-child, p:last-child{
        margin: 0;
    }
}

#admin-menu{
    div:last-child, p:last-child{
        margin: 0;
    }
}

button,
.button,
.field-node-link a,
.btn-primary,
.btn,
.btn-info,
.btn-default,
.field-name-field-website-address a,
.field-name-node-link a,
.field-name-field-button-links a,
.field-name-field-link a,
.field--name-node-link a{
        display: inline-block;
        padding: 10px;
        margin-right: 10px;
        margin-bottom: 10px;
        color: white !important;
        font-family: $bodyCopy;
        font-weight: 800;
        background: $cp3;
        @include font-size(15px);
        transition: all .5s ease;
        @include border-radius($radius);
        &:hover, &.hover{
            color: white !important;
            background: $cp2;
        }
}


//
// Lists
////// Ordered List
.region-content ol {
    list-style-type: none;

    margin: 0;
    margin-left: 0;
    padding: 0;

    counter-reset: li-counter;
}
.region-content ol > li{
    position: relative;
    margin-bottom: 15px;
    padding-left: 20px;
}
.region-content ol > li:before {
    position: absolute;
    top: 0;
    left: -1em;
    width: 25px;
    text-align: center;
    font-weight: 400;
    font-family: $bodyCopy;
    content: counter(li-counter)".";
    counter-increment: li-counter;
}

////// Unordered list
.region-content ul{
    margin: 0;
    padding: 0;
    li{
        margin-bottom: 15px;
        padding-left: 24px;
        position: relative;
        list-style-type: none;

        &:before{
            content: "";
            position: absolute;
            top: 7px;
            left: 0;
            width: 0;
            height: 0;
            border-top: 10px solid transparent;
            border-bottom: 10px solid transparent;
              border-left: 10px solid $cp2;
        }
    }
}

.chosen-container{
    ul li:before{
        content: none;
    }
}

.chosen-container-active .chosen-choices,.chosen-container-multi .chosen-choices, .chosen-container-active.chosen-with-drop .chosen-single{
    border: none;
    box-shadow: none;
    background: none;
}

.chosen-container .chosen-drop{
    left: 0;
    border: none;
}

.chosen-container-multi .chosen-choices li.search-choice{
    padding: 10px 25px 9px 15px;
    background: $cp3;
    font-weight: 700;
    border: 0;
    @include border-radius($radius);
    color: white;
}

.chosen-container-multi .chosen-choices li.search-choice .search-choice-close{
    top: 12px;
    right: 8px;
}

.chosen-container-single .chosen-single span{
    font-weight: 300;
}

.white-bg{
    background: rgba(255, 255, 255, .75);
    padding: 40px;
    @include border-radius($radius);
}

.expand {
    padding-top: 70px;
     padding-bottom: 70px;
}

.region-content img {
    @include border-radius($radius);
}

.front .region-content img {
    border-radius: 0 !important;
}

.front .region-content .paragraphs-items-field-teasers{
    img{
        border-radius: $radius !important;
    }
}


.float-right{
    float: right;
}


.rounded{
    @include border-radius(100%);
}
.background{
    padding: 50px 20px;
    background-size: cover;
    filter: blur(50%);
    position: relative;
}

.lettered li{
    list-style-type: lower-alpha;
}

// This fixes the :last-child margin removal for normal paragraphs
.main-container div.container:last-child{
    margin: 0 auto;
}

.group-left{
    padding-right: 20px;
}

.ds-2col > .group-left, .ds-2col > .group-right, .ds-2col-stacked > .group-left, .ds-2col-stacked > .group-right{
    width: 100%;
    padding: 0;
}

// Footer TODO: Remove Box shadow?
.region-footer{
    background: $cp1;
    min-height: 100px;
    box-shadow: 0px 500px 0px 500px $cp1;

    padding: 20px 20px 50px;
    p{
        @include font-size(14px);
        color: white;
    }
    a{
        color: $cp4 ;
        &:hover{
            color: $cp2;
        }
    }
}

// .row{
// 	max-width: 1200px;
// 	margin: 0 auto 20px !important;
// }

.footer{
    margin-top: 20px;
    border: 0;
}

// Forums

.form-required{
    color: red;
}

.webform-component{
    margin-bottom: 20px;
}

.webform-component-time{
    .form-control{
        display: inline-block;
        width: auto;
    }
    .minute{
        margin-left: 2px;
    }
}
// Radio buttons and checkboxes
.form-check-input, .form-inline .radio input[type="radio"]{
    -webkit-appearance: none !important;
    -moz-appearance: none !important;
    appearance: none !important;
    display: inline-block;
    position: relative;
    color: #666;
    top: 10px;
    height: 30px;
    background: none;
    width: 30px;
    border: 0;
    border-radius: 50px;
    cursor: pointer;
    margin-right: 7px;
    outline: none;

    &::before{
        position: absolute;
        font: 13px/1 'Open Sans', sans-serif;
        left: 0;
        content: ' ';
        //transform: rotate(40deg);
        background: $cp4;
        color: white;
        padding: 6px 0 0 8px;
        border-radius: 100%;
        height: 25px;
        width: 25px;
        z-index: 1;
    }
}
.form-check-input:checked::before,
.form-inline .radio input:checked::before{
    position: absolute;
    font: 13px/1 'Open Sans', sans-serif;
    left: 0;
    content: '\2713';
    // transform: rotate(40deg);
    background: $cp2;
    color: white;
    padding: 6px 0 0 8px;
    border-radius: 100%;
    height: 25px;
    width: 25px;
    z-index: 100;

}

.form-inline .radio input[type="radio"], .form-inline .checkbox input[type="checkbox"]{
    border: none;
}

label{
    font-weight: 300;
}

legend{
    @extend label;
    @include font-size(18px);
    border: none;
    margin: 0;
}

input[type="checkbox"]::before, input[type="checkbox"]:checked::before{
    border-radius: 0;
}

.form-check{
    label{
        padding-left: 20px;
    }
}

#edit-submitted-contact-time-ampm{
    margin-left: 5px;
}

.region-content{
    margin-bottom: 20px;
}

//
// Media
//
.media-float-left{
    img{
        margin: 0 20px 10px 0;
    }
}

.media_embed, .player{
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 60%;

    iframe{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}
@media (max-width: 768px){
    //
    // Tables
    table, thead, tbody, th, td, tr {
        display: block;
        border: none;
    }

    .table-responsive{
        border: none;
    }

    /* Hide table headers (but not display: none;, for accessibility) */
    thead tr {
        position: absolute;
        top: -9999px;
        left: -9999px;
    }

    tr { margin: 0 0 20px 0; }

    td {
        /* Behave  like a "row" */
        border: none;
        border-bottom: 1px solid #eee;
        position: relative;
        display: -webkit-box;
        flex: 2 50%;
        padding: 0 !important;
        vertical-align: middle;
        *{
            width: 48%;
            display: table-caption;
            white-space: normal;
        }
    }

    tr td:last-of-type{
        border-bottom: 4px solid $cp2
    }

    td:before {
        padding: 15px;
        display: block;
        width: 45% !important;
        margin-right: 5px;
        -ms-word-break: break-all;
        word-break: break-all;
        word-break: break-word;
        -webkit-hyphens: auto;
        -moz-hyphens: auto;
        hyphens: auto;
        padding-right: 10px;
        white-space: normal;
        font-weight: 800;
        background: $light;
        @include font-size(12px);
    }

    .table-responsive > .table > thead > tr > th, .table-responsive > .table > tbody > tr > th, .table-responsive > .table > tfoot > tr > th, .table-responsive > .table > thead > tr > td, .table-responsive > .table > tbody > tr > td, .table-responsive > .table > tfoot > tr > td{
        white-space: normal;
    }

    .table > thead > tr > th, .table > tbody > tr > th, .table > tfoot > tr > th, .table > thead > tr > td, .table > tbody > tr > td, .table > tfoot > tr > td{
        border: 1px solid #ddd;
    }

    .view-events{
        td:nth-of-type(1):before { content: "Event / Tournament"; }
        td:nth-of-type(2):before { content: "Date(s)"; }
        td:nth-of-type(3):before { content: "Registration"; }
        td:nth-of-type(4):before { content: "Price"; }
        td:nth-of-type(5):before { content: "Golf Course"; }
    }

    .view-instruction{
        td:nth-of-type(1):before { content: "Instruction opportunity"; }
        td:nth-of-type(2):before { content: "Date(s)"; }
        td:nth-of-type(3):before { content: "Price"; }
        td:nth-of-type(4):before { content: "Golf Course"; }
    }
    .view-promotions{
        td:nth-of-type(1):before { content: "Promotion"; }
        td:nth-of-type(2):before { content: "Date(s)"; }
        td:nth-of-type(3):before { content: "Golf Course"; }
    }

    .path-frontpage{
        .view-events{
            td:nth-of-type(1):before { content: "Event / Tournament"; }
            td:nth-of-type(2):before { content: "Date(s)"; }
            td:nth-of-type(3):before { content: "Registration"; }
            td:nth-of-type(4):before { content: "Golf Course"; }
        }
    }

    .table-striped > tbody > tr:nth-of-type(odd),.table-hover > tbody > tr:hover{
        background: none;
    }
    // Mike Overwrites
    td.views-field-field-registration-closes *,
    td.views-field-field-event-tournament-end-date *,
    td.views-field-field-end-date *{
        display: inline;
      }

    td h4 {
        display: block;
      }

      td {
        font-size: .8em;
        width: auto;
      }

      td a {
        line-height: normal;
        display: block;
      }
}
//
// Forms
//

input, textarea, .form-control, .form-textarea-wrapper textarea, select{
    width: 100%;
    height: auto;
    border: 1px solid darken($odd, 5%);
    padding: 10px 15px;
    @include border-radius($radius);
    font-family: $bodyCopy;
    @include font-size(16px);
    font-weight: 200;
    box-shadow: none;
    background: white;
    margin-bottom: 20px;
    color: $text-gray;
    -webkit-appearance: none;
    border:none;
    border-bottom: 3px solid $cp2;
    &:focus{
        outline: none;
        box-shadow: none;
        border-color: $cp4;
    }
}

.webform-component-textarea .grippie{
    display: none;
}

textarea.form-control{
    @include border-radius($radius);
}

.webform-submit{
    padding: 10px 15px;
}
// Pager
.pager{
    margin: 0 !important;
}

.pager li{
    display: inline-block;
    list-style: none;
}

.pager li a,
.pager .pager-current,
.pagination > .active > a, .pagination > .active > span,
.pagination > .active > a:hover, .pagination > .active > span:hover,
.pagination > .active > a:focus, .pagination > .active > span:focus{
    @extend button;
    background: black;
    padding: 5px 15px;
    height: auto;
    &:after{
        content: "";
        padding: 0;
    }
    &:hover{
        background: darken(black, 10%);
    }
}

.pager .pager-current{
    background: darken(black, 10%);
}

.pagination > li > a, .pagination > li > span{
    color: $text-gray;
}


// Accordion
.faq{
    margin-bottom: 20px;
}

// Closed State
.ui-state-default, .ui-widget-content .ui-state-default, .ui-widget-header .ui-state-default{
    background: white;
    border: 1px solid #e2e2e2;
}

.ui-state-default a:hover{
    color: $cp2;
}

// Open State
.ui-state-active{
    background: $cp1;
}

.ui-state-active a, .ui-state-active a:link, .ui-state-active a:visited{
color: white;
}



.ui-widget-content{
    border: 1px solid #e2e2e2;
}

.ui-corner-all, .ui-corner-bottom, .ui-corner-right, .ui-corner-br, .ui-corner-all, .ui-corner-top, .ui-corner-right, .ui-corner-tr{
    border-radius: 0;
}

//
// Random Glitch Fixes
//
#admin-menu-shortcuts-responsive{
    margin: 0 !important;
}

//
// Flexslider
//
.flexslider{
    border: none;
    margin: 0;
    padding: 0;
    background: none;
}
//
// Superfish
//
.sf-menu li:hover > ul, .sf-menu li.sfHover > ul{
    top: auto;
}
